.modal {
    z-index: 100;
    position: fixed;
    top: 22vh;
    left: 10%;
    width: 80%;
    background: lightskyblue;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
    border-radius: 8px;
    display: block;
    height: auto;
    padding: 5px;
}

.img {
    height: 400px;
    width: 440px
}

.modal__header {
    width: 100%;
    padding: 1rem 0.5rem;
    background: #2a006e;
    color: white;
}

.modal__header h2 {
    margin: 0.5rem;
}

.modal__content {
    padding: 1rem 4.5rem;
}

.modal__footer {
    padding: 1rem 0.5rem;
}

.modal-enter {
    transform: translateY(-10rem);
    opacity: 0;
}

.modal-enter-active {
    transform: translateY(0);
    opacity: 1;
    transition: all 200ms;
}

.modal-exit {
    transform: translateY(0);
    opacity: 1;
}

.modal-exit-active {
    transform: translateY(-10rem);
    opacity: 0;
    transition: all 200ms;
}

@media (min-width: 320px) and (max-width: 568px) {
    .modal__content {
        padding: 1rem 0.7rem;
        /* height: 100px; */
    }
    .img {
        height: 200px;
        width: 260px;
    }
    .modal {
        height: auto;
    }
}

@media (min-width: 375px) and (max-width: 667px) {
    .modal__content {
        padding: 1rem 0.7rem;
        /* height: 100px; */
    }
    .img {
        height: 200px;
        width: 275px;
    }
    .modal {
        height: auto;
    }
}

@media (min-width: 422px) and (max-width: 768px) {
    .modal__content {
        padding: 1rem 1.5rem;
        /* height: 100px; */
    }
    .img {
        height: 200px;
        width: 200px;
    }
    .modal {
        height: auto;
    }
}

@media (min-width: 411px) and (max-width: 768px) {
    .img {
        height: 200px;
        width: 300px;
    }
    .modal {
        height: auto;
    }
}

@media (min-width: 768px) {
    .modal {
        left: calc(50% - 20rem);
        width: 40rem;
        height: auto;
    }
}